/* Project section styling. This uses flexbox to have 4 containers in a 2x2 format
A title appears above each project and on click more project information is given */


#projects {
  /* 5rem is the height of the navbar */
  min-height: calc(100vh - 5rem);
  width: 100vw;
  max-width: 65rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  padding: 0 1rem;
}

.projects-title {
  margin-bottom: 6rem;
  font-size: 2.5rem;
  font-weight: 500;
}

.projects-container {
  display: flex;
  gap: 6rem;
  justify-content: center;
  align-items: center;
}
.project-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}
/* Image Styling */

.project-img {
  height: 11.5rem;
  width: 22rem;
  cursor: pointer;
  margin-bottom: 6rem;
}

.projects-see-more {
  padding: 0.5rem 2.5rem;
  border: none;
  background: #fff;
  border-radius: 1.6rem;
  font-size: 0.81rem;
  font-weight: 500;
  font-family: "Poppins", sans-serif;
}